import styles from "./Button.module.scss";

interface ButtonProps {
    /**
    * What is the type?
    */
    type: "primary" | "secondary" | "h3" | "h4" | "p" | "footer";
}

const Button = (props: any) => {
  const type = props.type;

  return (
    <button className={`${styles.base} ${styles[type]}`}>
      {props.children}
    </button>
  );
};

export default Button;
